<!--运营管理-->
<template>
  <div class="wrap">
    <van-nav-bar
      title="运营管理"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />
    <div class="bigBox">
      <div class="tabChange">
        <ul>
          <li v-for="(tab,index) in tabs" @click="toggle(index,tab.view)" :class="{active:active===index}">
            {{tab.type}}
          </li>
        </ul>
      </div>
      <div class="timeChoose">
        <div class="timeLeft">
          <div class="startTime" @click="chooseTime(0)">{{startTime}}</div>
          <span class="line"></span>
          <div class="endTime" @click="chooseTime(1)">{{endTime}}</div>
        </div>
        <div class="makeSure" @click="makeSure(true)">确定</div>
      </div>
    </div>

    <van-tabs swipeable animated title-active-color="#DDA944" @change="tabChange">
      <van-tab title="中诚信">
        <div class="lineBg"></div>
        <div class="titleBox">
          <div class="titleLeft">
            <div class="date">日期</div>
            <div class="users">注册用户数</div>
          </div>
          <div class="export">
            <img src="../../assets/images/mall/export.png" alt="">
          </div>
        </div>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh(null, true)">
          <div class="mainContent">
            <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
              <div class="boxItem">
                <div class="item" v-for="item in list">
                  <div class="itemTime">{{item.createTime}}</div>
                  <div class="itemNum">{{item.num}}</div>
                </div>
                <div class="item" v-for="item in list">
                  <div class="itemTime">{{item.createTime}}</div>
                  <div class="itemNum">{{item.num}}</div>
                </div>
                <div class="item" v-for="item in list">
                  <div class="itemTime">{{item.createTime}}</div>
                  <div class="itemNum">{{item.num}}</div>
                </div>
                <div class="item" v-for="item in list">
                  <div class="itemTime">{{item.createTime}}</div>
                  <div class="itemNum">{{item.num}}</div>
                </div>
                <div class="item" v-for="item in list">
                  <div class="itemTime">{{item.createTime}}</div>
                  <div class="itemNum">{{item.num}}</div>
                </div>
                <div class="item" v-for="item in list">
                  <div class="itemTime">{{item.createTime}}</div>
                  <div class="itemNum">{{item.num}}</div>
                </div>
                <div class="item" v-for="item in list">
                  <div class="itemTime">{{item.createTime}}</div>
                  <div class="itemNum">{{item.num}}</div>
                </div>
                <div class="item" v-for="item in list">
                  <div class="itemTime">{{item.createTime}}</div>
                  <div class="itemNum">{{item.num}}</div>
                </div>
              </div>
            </van-list>
          </div>
        </van-pull-refresh>
      </van-tab>

      <van-tab title="中债资信">
        <div class="lineBg"></div>
        <div class="titleBox">
          <div class="titleLeft">
            <div class="date">日期</div>
            <div class="users">注册用户数</div>
          </div>
          <div class="export">
            <img src="../../assets/images/mall/export.png" alt="">
          </div>
        </div>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh(null, true)">
          <div class="mainContent">
            <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
              <div class="boxItem">
                <div class="item" v-for="item in list">
                  <div class="itemTime">{{item.createTime}}</div>
                  <div class="itemNum">{{item.num}}</div>
                </div>
              </div>
            </van-list>
          </div>
        </van-pull-refresh>
      </van-tab>

      <van-tab title="国泰君安">
        <div class="lineBg"></div>
        <div class="titleBox">
          <div class="titleLeft">
            <div class="date">日期</div>
            <div class="users">注册用户数</div>
          </div>
          <div class="export">
            <img src="../../assets/images/mall/export.png" alt="">
          </div>
        </div>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh(null, true)">
          <div class="mainContent">
            <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
              <div class="boxItem">
                <div class="item" v-for="item in list">
                  <div class="itemTime">{{item.createTime}}</div>
                  <div class="itemNum">{{item.num}}</div>
                </div>
              </div>
            </van-list>
          </div>
        </van-pull-refresh>
      </van-tab>
    </van-tabs>
    <van-popup v-model="show">
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        :min-date="minDate"
        @confirm="makeSureTime"
        @cancel="cancel"
      />
    </van-popup>
  </div>
</template>

<script>
  import {formatDate} from "../../utils/date";
  import { Toast } from 'vant';

  export default {
        name: "yygl",
      components : {

      },
      data(){
        return {
          active:0,
          tabs:[
            {
              type:'注册统计',
              view:'1'
            },
            {
              type:'访问统计',
              view:'2'
            }
          ],
          loading:false,
          currentDate: new Date(),
          minDate: new Date(2019, 0, 1),
          isLoading: false,
          show: false, // 时间选择器
          startTime: '开始时间',
          endTime: '截止时间',
          status: '', // 开始截至状态
          finished: false,
          size: 10,
          page: 1,
          type: 1,
          shopType: 1,
          list: []
        }
      },
      methods:{
        onClickLeft () {
          this.$router.back();
        },
        // 切换统计列表
        toggle (i, v, time) {
          this.active = i;
          this.type = v
          if (this.startTime !== '开始时间' && this.endTime !== '截止时间') {
            time = true
          } else {
            time = null
          }
          this.getStatisticsData(time).then((res) => {

          })
        },
        tabChange(index, time) {
          this.shopType = index + 1
          if (this.startTime !== '开始时间' && this.endTime !== '截止时间') {
            time = true
          } else {
            time = null
          }
          this.getStatisticsData(time).then((res) => {

          })
        },
        // 下拉刷新
        onRefresh(time, e) {
          if (this.startTime !== '开始时间' && this.endTime !== '截止时间') {
            time = true
          } else {
            time = null
          }
          this.getStatisticsData(time, e).then((res) => {
            console.log(res)
            this.list = res.data
            this.$toast('刷新成功');
            this.isLoading = false;
          })
        },
        // 选择筛选时间弹出框
        chooseTime (e) {
          if (e === 0) {
            this.status = 'start'
          } else {
            this.status = 'end'
          }
          this.show = true
        },
        // 选择时间
        makeSureTime (e) {
          if (this.status === 'start') {
            this.startTime = formatDate(e, 'yyyy-MM-dd');
          } else if (this.status === 'end'){
            this.endTime = formatDate(e, 'yyyy-MM-dd');
          }
          this.show = false
        },
        // 取消时间选择器
        cancel () {
          this.show = false
        },
        //上拉加载
        onLoad() {
          this.getStatisticsData().then((res) => {
            if (this.page > 1) {
              this.list = this.list.concat(res.data);
            }
            // 加载状态结束
            this.loading = false;
            // 数据全部加载完成
            if (this.list.length >= res.totalCount) {
              this.finished = true;
            } else {
              this.page++;
            }
          });
        },
        // 点击确定
        makeSure (e) {
          if (this.startTime === '开始时间' || this.endTime === '截止时间') {
            this.$toast('请选择时间');
          } else {
            this.getStatisticsData(e)
          }
        },
        // 初始化统计数据
        getStatisticsData (time,e) {
          // 判断是否是下拉刷新
          if (e !== true) {
            Toast.loading({
              mask: false,
              message: '加载中...',
              duration: 0
            });
          }
          let data = {}
          if (time === true) {
            data = {
              pageNo: e ? 1 : this.page,
              pageSize: e ? this.page*this.size : this.size,
              shopType: this.shopType,
              type: this.type,
              startTime: this.startTime,
              endTime: this.endTime
            }
          } else {
            data = {
              pageNo: this.page,
              pageSize: this.size,
              shopType: this.shopType,
              type: this.type,
              startTime: null,
              endTime: null
            }
          }
          this.list = [];
          return new Promise((resolve, reject) => {
            this.$http.post(backstageIp + userUrl + '/selectFlagshipTotal', data).then(res => {
              if(res.status === 200){
                setTimeout(() => {
                  let data = res.data.data
                  this.list = data
                  Toast.clear()
                  return resolve(res.data)
                }, 500)
              }else{
                this.$toast('当前无网络连接或网络不稳定，请稍后重试');
              }
            }).catch(err => {
              this.$toast('当前无网络连接或网络不稳定，请稍后重试');
            });
          })
        }
      },
    mounted() {
          // this.getStatisticsData()
    }
  }
</script>

<style scoped>
  .tabChange {
    background: #F7F8FA;
    padding: 10px 0 11px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  ul{
    display:flex;
  }
  ul li{
    width:83px;
    height:30px;
    font-size: 15px;
    color: #202020;
    background: #fff;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    cursor:pointer
  }
  ul li:nth-of-type(1) {
    border-radius:30px 0px 0px 30px;
  }
  ul li:nth-of-type(2) {
    border-radius:0 30px 30px 0;
  }
  ul li.active{
    background:#DDA944;
    color: #fff;
  }
  .bigBox {
    margin-top: 46px;
    position: fixed;
    width: 100%;
    z-index: 999;
    background: #fff;
  }
  >>>.van-tabs__wrap {
    position: fixed;
    top: 140px;
  }
  .timeChoose {
    display: flex;
    justify-content: space-between;
    padding: 7px 15px;
    font-size: 12px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    border-bottom: 1px solid #f7f8fa;
  }
  .timeLeft {
    display: flex;
    align-items: center;
  }
  .startTime,.endTime {
    width: 100px;
    height: 27px;
    border-radius: 3px;
    background: #f7f7f7;
    text-align: center;
    line-height: 27px;
    color: #7c7c7d;
  }
  .line {
    width: 8px;
    height: 1px;
    background: #202020;
    margin: 0 8px;
  }
  .makeSure {
    width: 100px;
    height: 27px;
    border-radius:3px;
    text-align: center;
    line-height: 27px;
    border:1px solid rgba(221,169,68,1);
    color: #DDA944;
  }
  .van-picker {
    width: 240px;
  }
  .lineBg {
    height: 10px;
    background: #f7f8fa;
    position: fixed;
    width: 100%;
    top: 183px;
    z-index: 999;
  }
  .titleBox {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px 0 37px;
    position: fixed;
    top: 193px;
    width: 100%;
    box-sizing: border-box;
    z-index: 999;
    background: #fff;
  }
  .export {
    width: 65px;
    height: 38px;
  }
  .export img {
    width: 100%;
    height: 100%;
  }
  .titleLeft {
    display: flex;
    justify-content: space-between;
    flex: 1;
    margin-right: 33px;
    font-size:15px;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(32,32,32,1);
  }
  .van-pull-refresh {
    background: rgba(247, 248, 250, 1);
    margin-top: 186px;
  }
  .mainContent {
    min-height: calc(100vh - 232px);
  }
  .item {
    background: #fff;
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 10px 0;
    font-size:13px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(124,124,125,1);
    border-bottom: 1px solid #f0f0f0;
  }
  .item:nth-last-of-type(1) {
    border-bottom: none;
  }
  .itemNum {
    margin-right: 120px;
  }
  .boxItem {
    padding: 0 15px;
    background: #fff;
  }
  >>> .van-tabs__line {
    background: rgba(221, 169, 68, 1);
  }
</style>



